@import url('https://fonts.googleapis.com/css?family=Comfortaa');

header {
        min-height: 30vh;
    padding: 10vh 1vh;
}

.App {
  text-align: center;
  font-family: 'Comfortaa', cursive;
}

.App-logo {
  pointer-events: none;
  height: 33vmin;
}

.App-logo-small {
     height: 15vmin;
     align-self: flex-start;
}

.App-logo-small img{

}

.App-header {
  background-color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.cities {
  display: flex;
  margin: auto;
  max-width: 1000px;
  padding-top: 50px;
}

.cities ruby {
  margin: 20px auto;
  letter-spacing: 9px;
  font-size: 25px;
  font-family: 'Comfortaa', cursive;
    font-weight: 500;
}

.cities ruby rt {
    letter-spacing: 9px;
    font-size: 20px;
    font-family: 'Comfortaa', cursive;
    font-weight: 600;
}

.cities h4 {
    font-size: 25px;
}

.btn {
  font-family: 'Comfortaa', cursive;
  border-radius: 10px;
  background-color: #282c34;
  font-size: 25px;
  margin: 40px auto 0;
  color: white;
}

footer {
    min-height: 50vh;
    /*color: #e0dddd;*/
    color: white;
    overflow: hidden;
    background-color: #3f5251;
    position: relative;
    padding-bottom: 5vh;
    overflow: hidden;
}

footer::after {
    content: '';
    height: 4px;
    width: 100%;
    background-color: white;
    top: 10px;
    left: 0;
    position: absolute;
}

#enter {
    cursor: pointer;
    background-color: white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    height: 70px;
    width: 70px;
    background-image: linear-gradient(to right, #3f5251, #3f5251 50%, transparent 50%);
    background-size: 201% 100%;
    background-position: 100%;
    position: relative;
    text-decoration: none;
    color: #3f5251;
    font-family: 'Comfortaa', cursive;
    font-size: 20px;
    letter-spacing: 1px;
    transition: all .7s ease;
    margin: 20px auto 0;
}

#enter::after {
    content: '';
    height: 15px;
    width: 15px;
    background-color: white;
    top: 60px;
    left: -25px;
    transform: translateX(0%);
    position: absolute;
    border-radius: 50%;
}

#enter::before {
    content: '';
    height: 3px;
    width: 70%;
    background-color: white;
    top: 66px;
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
}

 #enter:hover {
    color: white;
     background-position: 0;
     font-size: 24px;
  }
 #enter:hover::after {
     left: 75px;
     transition: all .7s ease;
     -webkit-clip-path: polygon(100% 54%, 0 0, 0 100%);
     clip-path: polygon(100% 54%, 0 0, 0 100%);
 }

 .section-padding {

 }



 @media screen and (max-width: 500px) {
     .cities {
         flex-direction: column;
     }
     .cities h4 {
         display: none;
     }
     .cities ruby {
         font-size: 20px;
     }
     .cities ruby rt {
         font-size: 12px;
     }

     header {
             min-height: 10vh;
             padding: 10vh 1vh 5vh;
     }
 }




/*Nav Style*/
nav input {
  display: none;
}
nav label {
  position: fixed;
  right: 10px;
  top: 10px;
  z-index: 999;
  cursor: pointer;
}
nav label:hover b i:nth-child(2) {
  left: 0;
}
nav label strong {
  padding: 0 10px;
  width: 40px;
  height: 40px;
  line-height: 40px !important;
  font: 700 14px 'Comfortaa', cursive;
  color: #3f5251;
  text-transform: uppercase;
  position: absolute;
  right: 40px;
  top: 0;
  transition: color 250ms ease;
}
nav label b {
  width: 40px;
  height: 40px;
  display: block;
  position: relative;
  right: 0;
  top: 0;
  cursor: pointer;
  transition: background 500ms ease;
}
nav label b i {
  display: block;
  background: #3f5251;
  width: 24px;
  height: 4px;
  position: absolute;
  left: 8px;
  top: 11px;
  border-radius: 1000px;
  transform-origin: 20px 4px;
  transition: .5s all ease;
}
nav label b i:nth-child(2) {
  top: 18px;
  transition: .5s all ease;
}
nav label b i:nth-child(3) {
  transform-origin: 26px 4px;
  top: 25px;
}
nav menu {
  background: white;
  min-width: 300px;
  max-width: 33%;
  height: 100vh;
  opacity: 0;
  overflow: auto;
  transition: opacity 300ms ease;
  position: fixed;
  z-index: 998;
  top: 0;
  margin: 0;
  right: 0;
  padding: 0;
  box-shadow: -1px -1px 9px 2px rgba(136, 136, 136, 0.5);
}
nav menu li {
  margin: 0;
  list-style: none;
  width: 100%;
}
nav menu li:first-child {
  margin-top: 60px;
}
nav menu li:first-child a {
  border-top: #3f5251 1px solid;
}
nav menu li a {
  font-family: 'Comfortaa', cursive;
  text-transform: uppercase;
  text-decoration: none;
  color: #3f5251;
  display: block;
  border-bottom: #3f5251 1px solid;
  text-align: left;
  font-weight: 600;
  transition: all .4s ease;
  padding: 20px 10px;
}
nav menu li a:hover {
  /*background: rgba(0, 0, 0, 0.1);*/
    background-color: #779376a1;
    transition: .5s;
    transform: rotateZ(2deg);
       box-shadow: 18px 9px 11px -3px;
}
nav #nav:checked ~ label strong {

}
nav #nav:checked ~ label b {

}
nav #nav:checked ~ label b:hover {

}
nav #nav:checked ~ label b i:nth-child(1) {
  transform: rotate(45deg) translate(6px, 11px);
}
nav #nav:checked ~ label b i:nth-child(2) {
  opacity: 0;
}
nav #nav:checked ~ label b i:nth-child(3) {
  transform: rotate(-45deg) translate(10px, -14px);
}
nav #nav:checked ~ menu {
  opacity: 1;
}

